.resume {
    width: 920px;
    padding: 80px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-items: center;
    justify-self: center;
    align-self: center;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    font-family: Roboto,Helvetica,sans-serif;
    color: #000;
    font-size: 14px;
    line-height: 1.5;
    font-weight: 300;
    margin-bottom: 50px;
}

  @media screen and (max-width: 1000px) {
    .resume {
        width: 100%;
        padding: 20px;
    }
  }