.resume-header-meta {
    display: flex;
    flex-direction: row;
}
.resume-header-meta-inner {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-top: 10px;
}

.resume-header-meta-inner a {
    text-decoration: none;
    line-height: 1;
    color: #BB1E1E;
    padding-right: 10px;
}


@media screen and (max-width: 1000px) {
    .resume-header-meta-inner {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
  }